import { FunctionalComponent, h, VNode } from 'preact';
import { Text } from 'preact-i18n';
import Modal from 'src/shared/components/Modal';
import style from './style.module.scss';

type ModalProps = {
  open: boolean;
  onModalClose: () => void;
  title: VNode<unknown> | string;
};

const SuccessModal: FunctionalComponent<ModalProps> = ({ onModalClose, title, open }) => {
  return (
    <Modal open={open} onModalClose={onModalClose}>
      <div class={style['success-modal']}>
        <img class={style['success-modal__img']} src="assets/icons/success.svg" alt="Success" />
        <h2 class={style['success-modal__title']}>{title}</h2>

        <button class={style['success-modal__ok']} onClick={() => onModalClose()}>
          <Text id="modal.ok">OK</Text>
        </button>
      </div>
    </Modal>
  );
};

export default SuccessModal;
