import { FunctionalComponent, h, RefObject } from 'preact';
import { useRef } from 'preact/compat';
import { Animated } from '../Animated';
import { ModalPortal } from '../ModalPortal/ModalPortal';
import { useClickOutside } from '../../../core/hooks/useClickOutside';

interface ModalProps {
  open: boolean;
  onModalClose: (e: Event) => void;
}

const Modal: FunctionalComponent<ModalProps> = ({ open, onModalClose, children }) => {
  const ref: RefObject<HTMLElement> = useRef() as RefObject<HTMLElement>;
  useClickOutside(ref, onModalClose as () => void);

  return (
    <ModalPortal>
      <Animated render={open}>
        <div class={`modal__wrapper`}>
          <div className="modal__body" ref={ref as RefObject<HTMLDivElement>}>
            <div className="modal__close">
              <img
                className="modal__close"
                onClick={onModalClose}
                src="assets/icons/close.svg"
                alt="close"
              />
            </div>

            <div className="modal__content">{children}</div>
          </div>
        </div>
      </Animated>
    </ModalPortal>
  );
};

export default Modal;
