import { Fragment, FunctionalComponent, h } from 'preact';
import { useState } from 'preact/hooks';
import { createPortal } from 'preact/compat';
import { useFormik } from 'formik';
import style from 'src/routes/main/components/Subscription/components/SignupForm/style.module.scss';
import { Text } from 'preact-i18n';
import SuccessModal from '../../../../../../shared/components/Modal/components/SuccessModal';

const SignupForm: FunctionalComponent = () => {
  const [modal, setModal] = useState(false);
  const container = document.getElementById('modals');

  const formik = useFormik({
    initialValues: {
      email: ''
    },
    onSubmit: () => {
      setModal(true);
    }
  });

  return (
    <Fragment>
      <form
        class={style['signup-form']}
        id="subscribeToLetters"
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        onSubmit={formik.handleSubmit as any}
      >
        <input
          class={style['signup-form__input']}
          id="email"
          name="email"
          type="email"
          required
          // eslint-disable-next-line preact-i18n/no-text-as-attribute
          placeholder="Email address"
          onChange={formik.handleChange}
          value={formik.values.email}
        />

        <button class={style['signup-form__submit']} type="submit">
          <Text id="subscription.submit">Submit</Text>
          <img class={style.arrow} src="assets/icons/arrow-right.svg" alt="arrow" />
        </button>
      </form>
      {createPortal(
        <SuccessModal
          open={modal}
          onModalClose={() => {
            setModal(false);
          }}
          title={
            <Text id="modal.WeSentConfirmation">
              Thank you! We've sent a confirmation letter to your mail.
            </Text>
          }
        />,
        container as Element
      )}
    </Fragment>
  );
};

export default SignupForm;
